	import ButtonItems from './ButtonItems.vue'
    export default {
        props: {
            data: {
                type: Object,
                default: function(e) {
                    return {}
                }
            },
			options: {
				type: Object,
				default: {}
			}
        },
		components:{
			ButtonItems
		},
        methods: {
            close(e) {
                this.$emit('close');
                e.stopPropagation();
            },
            bindClick() {
                this.$emit('click');
            }
        }
    }
